#loading-page {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.lds-ellipsis,
.lds-ellipsis div {
    box-sizing: border-box;
}
.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 60px;
    height: 70px;
}
.lds-ellipsis div {
    position: absolute;
    top: 33.33333px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: rgb(253 96 50);
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
    left: 8px;
    -webkit-animation: lds-ellipsis1 0.4s infinite;
    animation: lds-ellipsis1 0.4s infinite;
}
.lds-ellipsis div:nth-child(2) {
    left: 8px;
    -webkit-animation: lds-ellipsis2 0.4s infinite;
    animation: lds-ellipsis2 0.4s infinite;
}
.lds-ellipsis div:nth-child(3) {
    left: 24px;
    -webkit-animation: lds-ellipsis2 0.4s infinite;
    animation: lds-ellipsis2 0.4s infinite;
}
.lds-ellipsis div:nth-child(4) {
    left: 40px;
    -webkit-animation: lds-ellipsis3 0.4s infinite;
    animation: lds-ellipsis3 0.4s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes lds-ellipsis1 {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@-webkit-keyframes lds-ellipsis3 {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@keyframes lds-ellipsis3 {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@-webkit-keyframes lds-ellipsis2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(20px, 0);
    }
}
@keyframes lds-ellipsis2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(20px, 0);
    }
}
