/* COMMON CSS */
* {
    box-sizing: inherit;
}

html {
    height: 100%;
    margin: 0;
    /* default: 16px => 1rem = 10px */
    font-size: 62.5%;
    line-height: 1.6rem;
    font-family: "Roboto", sans-serif;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    margin: 0;
    min-height: 100%;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

/* INPUT PLACEHOLDER */
::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
    opacity: 0.5;
}

/* NAVIGATION BUTTON */
.navigation-btn {
    width: 2.5rem;
    height: 2.5rem;
    background-color: #fff;
    border: none;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.08);
    box-shadow: 0 1px 12px 0 rgb(0 0 0 / 12%);
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(calc(-50%));
    display: flex;
    justify-content: center;
}
.navigation-btn__previous {
    left: 0;
    margin-left: calc(-1.25rem);
}
.navigation-btn__next {
    right: 0;
    margin-right: calc(-1.25rem);
}
.navigation-btn__icon {
    margin: auto 0;
    color: rgba(0, 0, 0, 0.54);
    font-size: 1rem;
}

/* ANIMATION */
@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-webkit-keyframes growth {
    from {
        opacity: 0;
        transform: scale(0);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}
@keyframes growth {
    from {
        opacity: 0;
        transform: scale(0);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

/* APP */
#app {
    position: absolute;
    inset: 0px;
}

/* 1. HEADER */
#header {
    width: 100%;
    background-color: #f94f2f;
    padding: 0.8rem 3.4rem 1rem 3rem;
    position: fixed;
    top: 0;
    z-index: 15;
}

/* 2. CONTAINER */
#container {
    position: relative;
    width: 100%;
    margin: 0;
    padding-top: 12rem;
    background-color: #f5f5f5;
}

/* 3. FOOTER */
#footer {
    border-top: 4px solid #ee4d2d;
}

/* 4. MOTION-PART */
#motion-part {
    width: 10rem;
    height: 16rem;
    position: fixed;
    bottom: 0;
    right: 0.8rem;
    z-index: 20;
}
