/* Sub Banner */
.motion-part__sub-banner {
    width: 8.2rem;
    height: 8.2rem;
    margin-bottom: 3rem;
}

.motion-part__sub-banner__link {
    display: block;
    width: 100%;
    height: 100%;
}

.motion-part__sub-banner__img {
    display: block;
    width: 100%;
    height: 100%;
}

/* Chat */
.motion-part__chat {
    width: 10rem;
    height: 4.8rem;
    background-color: #fff;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    box-shadow: 0 0 5px rgb(0 0 0 / 20%);
}

.motion-part__chat__main {
    width: 100%;
    height: 100%;
    padding: 1.2rem;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.motion-part__chat__main__icon {
    fill: #ee4d2d;
    width: 2.4rem;
    height: 2.4rem;
}

.motion-part__chat__main__text {
    fill: #ee4d2d;
    width: 4.4rem;
    height: 2.2rem;
}
